import { SelectTab } from '@cetus/design'
import { useNavigate } from 'react-router-dom'
import { TradeTabs } from '../swap/type'

const tradeTabList = [
  {
    label: TradeTabs.Swap
  },
  {
    label: TradeTabs.Limit
  },
  {
    label: TradeTabs.DCA
  }
]

type TradeTab = {
  currTradeTab: TradeTabs
}

export function TradeTab(props: TradeTab) {
  const { currTradeTab } = props
  const navigate = useNavigate()
  return (
    <SelectTab
      type="borderTab"
      currentTab={currTradeTab}
      tabList={tradeTabList}
      handleChangeTab={tab => {
        if (tab.label === TradeTabs.Limit) {
          navigate(`/limit`)
        } else if (tab.label === TradeTabs.DCA) {
          navigate(`/dca`)
        } else {
          navigate(`/swap`)
        }
      }}
      wrapStyle={{
        bg: 'none',
        h: '52px',
        gap: '32px',
        border: 'none'
      }}
      itemStyle={{
        fontSize: '16px',
        fontWeight: 500,
        position: 'relative'
      }}
    />
  )
}
